.btn-primary{
    background: none;
    padding: .5rem 1.2rem;
    border-radius: var(--br-sm);
    border: none;
    border: 1px solid #DFE1E4;
    box-shadow: 0px 3px 0px #DFE1E4;
    transition: all .3s;
    color: #5C6066;
    font-size: 1.2rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: .65rem;
    font-family: inherit;
    &:active{
        box-shadow: 0px 1.5px 0px #DFE1E4;
        transform: translateY(3px);
    }

    & svg{
        fill: #5C6066;
        width: 1.4rem;
        height: 1.4rem;
    }

    &--divider{
        gap: 1.8rem;
        position: relative;
        &::after{
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            height: 45%;
            width: 1px;
            background-color: #DFE1E4;
            transform: translate(-50%,-50%);
        }
    }
}